function inflect (number, nouns, withNumber = true) {
  let noun = nouns[0]
  if (number === 1) {
    noun = nouns[1]
  } else if (number % 100 === 12 || number % 100 === 13 || number % 100 === 14) {
    noun = nouns[2]
  } else if (number % 10 === 2 || number % 10 === 3 || number % 10 === 4) {
    noun = nouns[3]
  }

  if (!withNumber) {
    return noun
  }

  return number + ' ' + noun
}

export default {
  methods: {
    inflectBrewery (number, withNumber = true) {
      return inflect(number, ['browarw', 'browar', 'browarw', 'browary'], withNumber)
    },
    inflectCountry (number, withNumber = true) {
      return inflect(number, ['pastw', 'pastwo', 'pastw', 'pastwa'], withNumber)
    },
    inflectTrip (number, withNumber = true) {
      return inflect(number, ['wycieczek', 'wycieczka', 'wycieczek', 'wycieczki'], withNumber)
    },
    inflectCity (number, withNumber = true) {
      return inflect(number, ['miast', 'miasto', 'miast', 'miasta'], withNumber)
    },
    inflectTag (number, withNumber = true) {
      return inflect(number, ['tagw', 'tag', 'tagw', 'tagi'], withNumber)
    },
    inflectCountries (number, withNumber = true) {
      return inflect(number, ['pastwach', 'pastwie', 'pastwach', 'pastwach'], withNumber)
    },
    inflectVisitedBreweries (number, withNumber = true) {
      return inflect(number, ['odwiedzonych browarw', 'odwiedzony browar', 'odwiedzonych browarw', 'odwiedzone browary'], withNumber)
    },
    inflectVisitedTrips (number, withNumber = true) {
      return inflect(number, ['odbytych wycieczek', 'odbyta wycieczka', 'odbytych wycieczek', 'odbyte wycieczki'], withNumber)
    },
    inflectVisitedCities (number, withNumber = true) {
      return inflect(number, ['zwiedzonych miast', 'zwiedzone miasto', 'zwiedzonych miast', 'zwiedzone miasta'], withNumber)
    },
    inflectVisitedCountries (number, withNumber = true) {
      return inflect(number, ['odwiedzonych pastw', 'odwiedzone pastwo', 'odwiedzonych pastw', 'odwiedzone pastwa'], withNumber)
    }
  }
}
